"use client";

import React from "react";
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip as RechartsTooltip, // Renamed to avoid conflict
  ResponsiveContainer,
  Legend,
  RadialBarChart,
  RadialBar,
  PolarAngleAxis,
} from "recharts";
import {
  BriefcaseIcon,
  LineChart,
  TrendingUp,
  TrendingDown,
  Brain,
  Zap, // Added for "In-Demand"
  Lightbulb, // Added for "Recommended"
  ArrowRight, // Added for "Trends"
} from "lucide-react";
import { format, formatDistanceToNow } from "date-fns";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
// Assuming Tooltip is available from shadcn/ui
import {
  Tooltip,
  TooltipContent,
  TooltipProvider,
  TooltipTrigger,
} from "@/components/ui/tooltip";

const DashboardView = ({ insights }) => {
  // --- Data Transformations ---

  // Salary data for Bar Chart
  const salaryData = insights.salaryRanges.map((range) => ({
    name: range.role,
    min: range.min / 1000,
    max: range.max / 1000,
    median: range.median / 1000,
  }));

  // Data for the new Radial Chart (Industry Growth)
  const growthRateData = [
    {
      name: "Growth",
      value: insights.growthRate,
      fill: "hsl(var(--primary))", // Theme-aware color
    },
  ];
  const roundedGrowthRate = insights.growthRate.toFixed(1);

  // --- Helper Functions (from original) ---

  const getDemandLevelStyle = (level) => {
    switch (level.toLowerCase()) {
      case "high":
        return {
          bar: "bg-green-500",
          text: "text-green-600 dark:text-green-400",
        };
      case "medium":
        return {
          bar: "bg-yellow-500",
          text: "text-yellow-600 dark:text-yellow-400",
        };
      case "low":
        return {
          bar: "bg-red-500",
          text: "text-red-600 dark:text-red-400",
        };
      default:
        return {
          bar: "bg-gray-500",
          text: "text-gray-600 dark:text-gray-400",
        };
    }
  };

  const getMarketOutlookInfo = (outlook) => {
    switch (outlook.toLowerCase()) {
      case "positive":
        return { icon: TrendingUp, color: "text-green-500" };
      case "neutral":
        return { icon: LineChart, color: "text-yellow-500" };
      case "negative":
        return { icon: TrendingDown, color: "text-red-500" };
      default:
        return { icon: LineChart, color: "text-gray-500" };
    }
  };

  // --- Style & Date Variables ---

  const { icon: OutlookIcon, color: outlookColor } = getMarketOutlookInfo(
    insights.marketOutlook
  );
  const { bar: demandBarColor, text: demandTextColor } = getDemandLevelStyle(
    insights.demandLevel
  );

  const lastUpdatedDate = format(new Date(insights.lastUpdated), "MMM dd, yyyy");
  const nextUpdateDistance = formatDistanceToNow(
    new Date(insights.nextUpdate),
    { addSuffix: true }
  );

  return (
    <TooltipProvider>
      <div className="p-4 sm:p-6 md:p-8 bg-background min-h-screen">
        {/* === HEADER === */}
        <div className="flex flex-col sm:flex-row justify-between sm:items-center mb-6 gap-4">
          <div>
            <h1 className="text-3xl font-bold tracking-tight">
              Industry Insights Dashboard
            </h1>
            <p className="text-muted-foreground">
              A creative overview of the current market landscape.
            </p>
          </div>
          <Badge
            variant="outline"
            className="text-sm py-2 px-3 w-fit sm:w-auto"
          >
            Last updated: {lastUpdatedDate}
          </Badge>
        </div>

        {/* === MAIN GRID LAYOUT === */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* --- Main Content (Left Column) --- */}
          <div className="lg:col-span-2 flex flex-col gap-6">
            {/* Top Stats Grid */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              {/* Market Outlook Card */}
              <Card className="transition-all duration-300 ease-in-out hover:shadow-lg">
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium text-muted-foreground">
                    Market Outlook
                  </CardTitle>
                  <OutlookIcon className={`h-5 w-5 ${outlookColor}`} />
                </CardHeader>
                <CardContent>
                  <div className="text-3xl font-bold">
                    {insights.marketOutlook}
                  </div>
                  <p className="text-xs text-muted-foreground pt-1">
                    Next update {nextUpdateDistance}
                  </p>
                </CardContent>
              </Card>

              {/* Demand Level Card */}
              <Card className="transition-all duration-300 ease-in-out hover:shadow-lg">
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium text-muted-foreground">
                    Demand Level
                  </CardTitle>
                  <BriefcaseIcon
                    className={`h-5 w-5 ${demandTextColor}`}
                  />
                </CardHeader>
                <CardContent>
                  <div className={`text-3xl font-bold ${demandTextColor}`}>
                    {insights.demandLevel}
                  </div>
                  <Tooltip>
                    <TooltipTrigger asChild>
                      <div
                        className={`h-2 w-full rounded-full mt-2 ${demandBarColor}`}
                      />
                    </TooltipTrigger>
                    <TooltipContent>
                      <p>
                        Current demand level in the industry: {insights.demandLevel}
                      </p>
                    </TooltipContent>
                  </Tooltip>
                </CardContent>
              </Card>

              {/* Industry Growth (Radial Chart) */}
              <Card className="transition-all duration-300 ease-in-out hover:shadow-lg">
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium text-muted-foreground">
                    Industry Growth
                  </CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="h-40 w-full relative -mt-4">
                    <ResponsiveContainer width="100%" height="100%">
                      <RadialBarChart
                        innerRadius="70%"
                        outerRadius="100%"
                        barSize={16}
                        data={growthRateData}
                        startAngle={90}
                        endAngle={450}
                      >
                        <PolarAngleAxis
                          type="number"
                          domain={[0, 100]}
                          angleAxisId={0}
                          tick={false}
                        />
                        <RadialBar
                          background
                          dataKey="value"
                          cornerRadius={8}
                          angleAxisId={0}
                        />
                      </RadialBarChart>
                    </ResponsiveContainer>
                    <div className="absolute inset-0 flex flex-col items-center justify-center">
                      <span className="text-4xl font-bold text-primary">
                        {roundedGrowthRate}%
                      </span>
                      <span className="text-xs text-muted-foreground">
                        YoY Growth
                      </span>
                    </div>
                  </div>
                </CardContent>
              </Card>
            </div>

            {/* Salary Ranges Chart */}
            <Card className="col-span-4 transition-all duration-300 ease-in-out hover:shadow-lg">
              <CardHeader>
                <CardTitle className="text-xl font-semibold">
                  Salary Ranges by Role
                </CardTitle>
                <CardDescription>
                  Displaying minimum, median, and maximum salaries (in $1,000s)
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="h-[400px]">
                  <ResponsiveContainer width="100%" height="100%">
                    <BarChart data={salaryData}>
                      <CartesianGrid
                        strokeDasharray="3 3"
                        stroke="hsl(var(--border))"
                      />
                      <XAxis
                        dataKey="name"
                        stroke="hsl(var(--muted-foreground))"
                      />
                      <YAxis
                        stroke="hsl(var(--muted-foreground))"
                        unit="k"
                      />
                      <RechartsTooltip
                        content={({ active, payload, label }) => {
                          if (active && payload && payload.length) {
                            return (
                              <div className="bg-popover text-popover-foreground border border-border rounded-lg p-3 shadow-lg">
                                <p className="font-bold text-lg mb-2">{label}</p>
                                {payload.map((item) => (
                                  <p
                                    key={item.name}
                                    className="text-sm"
                                    style={{ color: item.fill }}
                                  >
                                    {item.name}: ${item.value}K
                                  </p>
                                ))}
                              </div>
                            );
                          }
                          return null;
                        }}
                      />
                      <Legend />
                      <Bar
                        dataKey="min"
                        fill="hsl(var(--primary) / 0.4)"
                        name="Min Salary (K)"
                        radius={[4, 4, 0, 0]}
                      />
                      <Bar
                        dataKey="median"
                        fill="hsl(var(--primary))"
                        name="Median Salary (K)"
                        radius={[4, 4, 0, 0]}
                      />
                      <Bar
                        dataKey="max"
                        fill="hsl(var(--primary) / 0.8)"
                        name="Max Salary (K)"
                        radius={[4, 4, 0, 0]}
                      />
                    </BarChart>
                  </ResponsiveContainer>
                </div>
              </CardContent>
            </Card>
          </div>

          {/* --- Sidebar (Right Column) --- */}
          <div className="lg:col-span-1 flex flex-col gap-6">
            {/* Top Skills Card */}
            <Card className="transition-all duration-300 ease-in-out hover:shadow-lg">
              <CardHeader className="flex flex-row items-center space-x-3 space-y-0 pb-4">
                <Zap className="h-6 w-6 text-primary" />
                <CardTitle className="mb-0 text-lg font-semibold">
                  In-Demand Skills
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="flex flex-wrap gap-2">
                  {insights.topSkills.map((skill) => (
                    <Badge key={skill} variant="default" className="text-sm">
                      {skill}
                    </Badge>
                  ))}
                </div>
              </CardContent>
            </Card>

            {/* Recommended Skills Card */}
            <Card className="transition-all duration-300 ease-in-out hover:shadow-lg">
              <CardHeader className="flex flex-row items-center space-x-3 space-y-0 pb-4">
                <Lightbulb className="h-6 w-6 text-yellow-500" />
                <CardTitle className="mb-0 text-lg font-semibold">
                  Recommended Skills
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="flex flex-wrap gap-2">
                  {insights.recommendedSkills.map((skill) => (
                    <Badge key={skill} variant="outline" className="text-sm">
                      {skill}
                    </Badge>
                  ))}
                </div>
              </CardContent>
            </Card>

            {/* Key Industry Trends Card */}
            <Card className="transition-all duration-300 ease-in-out hover:shadow-lg">
              <CardHeader>
                <CardTitle className="text-lg font-semibold">
                  Key Industry Trends
                </CardTitle>
                <CardDescription>
                  Current movements shaping the industry
                </CardDescription>
              </CardHeader>
              <CardContent>
                <ul className="space-y-4">
                  {insights.keyTrends.map((trend, index) => (
                    <li key={index} className="flex items-start space-x-3">
                      <ArrowRight className="h-5 w-5 flex-shrink-0 text-primary mt-0.5" />
                      <span className="text-sm">{trend}</span>
                    </li>
                  ))}
                </ul>
              </CardContent>
            </Card>

            {/* Original Top Skills Card (hidden, but here to show I didn't use it) */}
            {false && (
              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">
                    Top Skills
                  </CardTitle>
                  <Brain className="h-4 w-4 text-muted-foreground" />
                </CardHeader>
                <CardContent>
                  <div className="flex flex-wrap gap-1">
                    {insights.topSkills.map((skill) => (
                      <Badge key={skill} variant="secondary">
                        {skill}
                      </Badge>
                    ))}
                  </div>
                </CardContent>
              </Card>
            )}
          </div>
        </div>
      </div>
    </TooltipProvider>
  );
};

export default DashboardView;
